.address {
  width: 100%;
  margin: auto;
}

@media (min-width: $widthDesktop) {
  .address {
    position: relative;
  }
}

.address--cat {
  background-color: $secondaryBackColor;
}

.address__wrapper {
  display: flex;
  align-items: center;
  padding-top: 28px;
  padding-bottom: 26px;
  width: 320px;
  margin: auto;
}

@media (min-width: $widthTablet) {
  .address__wrapper {
    width: 628px;
    justify-content: space-between;
    margin: auto;
    padding: 63px 70px 58px;
  }
}

@media (min-width: $widthDesktop) {
  .address__wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-107%, -53%);
    width: 410px;
    z-index: 100;
    background-color: $baseColor;
    padding-left: 75px;
    padding-right: 85px;
  }
}

.address__invite {
  text-transform: uppercase;
  font-size: 16px;
  width: 130px;
  line-height: 1.25;
  margin: 0;
  margin-right: 11px;
  margin-left: 19px;
}

@media (min-width: $widthTablet) {
  .address__invite {
    font-size: 20px;
    width: 154px;
    margin-left: 0;
  }
}

.address__location {
  font-family: $fontSecondary;
  font-size: $fontBaseSize;
  width: 140px;
  line-height: 1.43;
  margin: 0;
}

@media (min-width: $widthTablet) {
  .address__location {
    font-size: 16px;
    width: 160px;
    text-align: right;
  }
}

.address__map {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  background: $secondaryBackColor url("../img/bg-map-mobile@1x.png") no-repeat center center / cover;
  height: 362px;

  // &::after {
  //   content: "";
