.address__loc-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: $widthTablet) {
  .address__location:first-child {
    margin-bottom: 14px;
  }
}

@media (min-width: $widthDesktop) {
  .address__location {
    text-align: left;
  }
}
